@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
*, *::before, *::after {
  box-sizing: border-box;
}

body {
  font-family: 'Montserrat', sans-serif;
  margin: 0;
  background: url(/static/media/neutral.c245c8ab.jpg)
}

.login-statement {
  text-align: center;
}

.polls {
  margin: auto;
}

.poll {
  margin: 75px 0;
}

.poll-title {
  text-align: center;
  font-size: 30px;
  margin: 40px;
}

.poll-options {
  display: flex;
}

.poll-button {
  width: 50%;
  padding: 1em 0;
  background:#d0efff;
  font-size: 16px;
  border-radius: 10px;
}

.poll-button:hover {
  cursor: pointer;
}

.poll-button:disabled:hover {
  cursor: default;
}

.poll-results {
  display: none;
  text-align: center;
}

.poll-results p {
  font-weight: 700;
  font-size: 18px;
}

.votes-container {
  display: inline;
}

.votes {
  display: flex;
  justify-content: space-around;
}

.active {
  background: #2a9df4;
}

.voted .poll-results {
  display: inline;
}

.owned {
  position: relative;
}

.trash-icon {
  position: absolute;
  top: 0;
  right: 0;
  height: 2.5em;
}

.trash-icon:hover {
  cursor: pointer;
}

.poll-create {
  display: flex;
  flex-direction: column;
  width: 60%;
  margin: auto;
  margin-bottom: 100px;
}

.poll-create h1 {
  text-align: center;
}

#poll-form {
  display: flex;
}

#poll-form input {
  height: 3em;
  border-radius: 5px;
}

.form-error {
  color: red;
}

@media only screen and (min-width: 901px) {
  .polls {
    width: 70%;
  }
}

@media only screen and (max-width: 900px) {
  .polls {
    width: 80%;
  }
}

nav {
  display: flex;
  background: brown;
  justify-content: space-between;
  font-size: 20px;
}
  
nav a {
  text-decoration: none;
  color: black;
  margin-top: 20px;
}
  
nav ul {
  display: flex;
  list-style: none;
}

@media only screen and (min-width: 651px) {
  nav {
    padding: 0 100px;
  }

  nav ul li {
    margin-left: 100px;
  }
}

@media only screen and (max-width: 650px) {
  nav {
    padding: 0 50px;
  }

  nav ul li {
    margin-left: 50px;
  }
}
.register-container {
    display: flex;
    flex-direction: column;
    height: 350px;
    margin: auto;
    background: #013082;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    opacity: 0.9;
    color: white;
    font-family: 'Montserrat', sans-serif;
    padding: 20px 40px;
}

.register-container h1 {
    text-align: center;
}

.register-form {
    display: flex;
    flex-direction: column;
}

.register-form input {
    margin: 10px 0;
    width: 100%;
    height: 2em;
    font-size: 16px;
    border: none;
}

.register-form-submit input {
    margin-top: 30px;
    color: white;
    background: #18b9cb;
}

.register-error {
    color: red;
    text-align: center;
    margin-top: 0;
}

@media only screen and (min-width: 561px) {
    .register-container {
        width: 500px;
    }
}

@media only screen and (min-width: 461px) and (max-width: 560px) {
    .register-container {
        width: 400px;
    }
}

@media only screen and (min-width: 361px) and (max-width: 460px) {
    .register-container {
        width: 300px;
    }
}

@media only screen and (min-width: 261px) and (max-width: 360px) {
    .register-container {
        width: 200px;
    }
}
